<template>
    <div class='all'>
        <div class='cardContainer'>
        <v-container fluid>
        <v-row dense>
          <v-col
            v-for="card in cards"
            :key="card.title"
            :cols="card.flex"
          >
            <v-card>
              <v-img
                :src="card.src"
                class="white--text align-end"
                gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                height="200px"
              >
              <v-card-title v-text="card.title"></v-card-title>
              </v-img>
              <v-card-actions>
              <v-spacer></v-spacer>
              <v-dialog
                v-model="dialog"
                width="500"
              >
              <template v-slot:activator="{ on }">
                  <v-btn
                    color="blue lighten-2"
                    v-on="on"
                    remove_red_eye
              
                  >
                  <v-icon color='white'>remove_red_eye</v-icon>
                </v-btn>
              </template>
              <v-card>
                <v-card-title
                  class="headline grey lighten-2"
                  primary-title
                >
                  About
                </v-card-title>
                <v-card-text>
                  Welcome to my Vue.JS app. This app began as an excersize and was originally simply meant to test out some a new framework and grew from there. Feel free to browse the 12,000+ products and see the amazing things that Vue can do.
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                <v-spacer></v-spacer>
                  <v-btn
                    color="primary"
                    text
                    @click="dialog = false"
                  >
                    Cool!
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
  <v-divider></v-divider>
  <!--- FAQ ---> 
  <p class='title' style='font-weight:bold; text-align:center'> Project Overview </p>
   <v-divider></v-divider>
          <v-expansion-panels focusable>
            <v-expansion-panel
              v-for="item in items"
              :key="item.title"
            >         
              <v-expansion-panel-header v-text='item.title'> 
              </v-expansion-panel-header>
              <v-expansion-panel-content v-text='item.text'>
              </v-expansion-panel-content>
            </v-expansion-panel>
        </v-expansion-panels>
      </div>
<p class='title' style=' margin-top: 5vh; margin-bottom: 5vh; font-weight:bold; text-align:center'> How it Works</p>     
<!--- STEPPERS ---> 
<v-divider></v-divider>
  <v-stepper v-model="e1" style='margin-bottom: 10vh;'>
    <v-stepper-header>
      <v-stepper-step :complete="e1 > 1" step="1">Request from open API</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step :complete="e1 > 2" step="2">Store to dB</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step step="3">Utilize Data</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step step="4">Continue</v-stepper-step>
    </v-stepper-header>
<!------------- STEP ONE ---------------->     
    <v-stepper-items>
      <v-stepper-content step="1">
        <v-card
          class="mb-12"
          color="grey darken-1"
          height="200px"
        >
        <v-card-subtitle style='color:white; font-size:18px'>By monitoring network traffic, an open API is found. Then, by manipulating the URL we can request 100s of items, update search params and identify unique category IDs </v-card-subtitle>
        </v-card>
        <v-btn
          color="primary"
          @click="e1 = 2"
        >
          Next Step
        </v-btn>
      </v-stepper-content>
<!------------- STEP TWO ----------------> 
      <v-stepper-content step="2">
        <v-card
          class="mb-12"
          color="grey darken-1"
          height="200px"
        >
          <v-card-subtitle style='color:white; font-size:18px'>
            The items are then sent to our Firebase dB where they can be called from the dB instead of API calls to a 3rd party
          </v-card-subtitle>
        </v-card>
        <v-btn
          color="primary"
          @click="e1 = 3"
        >
          Next Step
        </v-btn>
      </v-stepper-content>
<!------------- STEP THREE ----------------> 
      <v-stepper-content step="3">
        <v-card
          class="mb-12"
          color="grey darken-1"
          height="200px"
        >
          <v-card-subtitle style='color:white; font-size:18px'>
            The items are populated using our templates where we can call specific JSON data that we've stored in the dB. This includes, pricing, quantity pricing, discounts, technical specs, images and more
          </v-card-subtitle>
        </v-card>
        <v-btn
          color="primary"
          @click="e1 = 4"
        >
          Next Step
        </v-btn>
      </v-stepper-content>
<!------------- STEP FOUR ----------------> 
      <v-stepper-content step="4">
        <v-card
          class="mb-12"
          color="grey darken-1"
          height="200px"
        >
          <v-card-subtitle style='color:white; font-size:18px'>
            Product data is sourced from multiple sites, each using different terminology requiring many 'if' statements. The main goal would be to create an affiliate shopping site with the data. The product images, nor the technical specs belong to any one eCommerce site
          </v-card-subtitle>
        </v-card>
        <v-btn
          color="primary"
          @click="e1 = 4"
        >
          Done!
        </v-btn>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
<!---- ENDING DIV --->
    </div>
</template>


<script>
  export default {
    methods: {
        next () {
            alert('You clicked next!')
    }
    },
    data() {
     return {
       e1: 1,
  
   

        cards: [
            { title: 'About', src: 'https://www.bacancytechnology.com/blog/wp-content/uploads/2019/04/vuejs-plugins-blog-banner.jpg', flex: 12 },

        ],
        items: [
            { title: 'Technologies Used', text: 'Front End: Vue.JS, Vuetify/Materialize CSS, Skeleton Loading, Lazy Loading, Backend: Serverless (Firebase) '},         
            { title: 'Functionality', text: 'Call to API -> Format & Call Data to this site -> Admin can push to dB -> DL all data to CSV'},
            { title: 'Project Status', text: 'While still new, this project is nearly fully operational. There are a ton of things I would like to do from an Admin perspective such as an admin dashboard, setting up cron jobs to run for updates, caching, and moving the dB from Firestore to something less "real-time" - Additionally, either by scraping or API calls I would like to gather and show historical pricing data as well as creating a users route so users can save items and track pricing/get email alerts'},
        ],
      
    }
     }
    

  }
</script>

<style scoped>

.cardContainer{
   
    height: auto;
}
.v-expansion-panel-content {
    margin-left: 3vw;
    margin-bottom: 2vh;
}

</style>


