<template>
    <div class="all">
        <div class="loader" v-if="loading">
            <v-sheet :color="`grey ${theme.isDark ? 'darken-2' : 'lighten-4'}`" class="px-5 py-3">
                <v-skeleton-loader
                    class="mx-auto"
                    max-width="500"
                    type="card"
                ></v-skeleton-loader>
            </v-sheet>
        </div>
        <template v-else>
 <!--- TOP PRODUCT PREVIEW --->
  <v-card class="inline-block mx-auto">
    <v-container>
      <v-row justify="space-between">
        <v-col cols="auto">
          <v-img
            height="auto"
            width="300"
            :src="product.largeImagePath"
          ></v-img>
        </v-col>
        <v-col
          cols="auto"
          class="text-left"
        >
        <v-row
          class="flex-column ma-0 fill-height"
          justify="center"
        >
        <v-col class='text-left'>
          <v-card-title class='text-left'>{{product.shortDescription}}</v-card-title>
        </v-col>
        
            <v-col class="text-right">
              <!--<v-text>Brand: {{product.brand.name}}</v-text>-->
              <v-card-subtitle class="text-right py-0" style='text-decoration: line-through' v-if='product.pricing.unitListPriceDisplay != null'> List:{{product.pricing.unitListPriceDisplay}}</v-card-subtitle>
              <p style='display:none' v-else></p>
              <p style="color:red; font-weight:bold" v-if='product.pricing.actualPrice != 0'>Common Online Price ${{product.pricing.actualPrice}}</p>
              <p style='color:red;font-weight:bold' v-else> Log in to see Pricing Info </p>
              <!---<v-card-subtitle class="text-right py-0" v-if='product.countryOfOrigin != undefined'> Manufactured In:{{product.countryOfOrigin}}</v-card-subtitle>
              <p style='display:none' v-else></p>--->
            </v-col>
              
<!--- FIRST BUTTON ---> 
                <div class="text-right">
                    <v-dialog
                      v-model="dialog"
                      width="500"
                    >
                    <template v-slot:activator="{ on }">
                      <v-btn
                        color="yellow darken-2"
                        style='color:black; width:10vw'
                        dark
                        v-on="on"
                      >
                      <v-icon>shopping_cart</v-icon>Amazon $-
                          
                      </v-btn>
                    </template>
                    <v-card>
                      <v-card-title
                        class="headline yellow darken-2"
                        primary-title
                        style='text-decoration:none'
                      >
                        Amazon
                      </v-card-title>
                      <v-card-text style='color:black; margin-top: 2%'>
                        This WOULD be a link to this product on Amazon as well as a current price if I had access to the Amazon API, which I don't. In order to get API access I would need to make 3 affiliate sales with ( assumedly ) a hard-coded link. Since all of these products are rendered programmatically, that is not an option.        
                      </v-card-text>
                      <v-divider></v-divider>
                    </v-card>
                  </v-dialog>
                </div>
          <!--- SECOND BUTTON --->
                <div class='text-right'>
                  <v-dialog
                  v-model="dialog"
                  width="500"
                  >
                  <template v-slot:activator="{ on }">
                    <v-btn
                      color="blue darken-4"
                      style='color:yellow; width:10vw; text-decoration:none'
                      dark
                      v-on="on"
                    >
                      <v-icon>shopping_cart</v-icon>Walmart $-
                    </v-btn>
                  </template>
                  <v-card>
                    <v-card-title
                      class="headline blue darken-4"
                      primary-title
                      style='color:yellow'
                    >
                      Walmart
                  </v-card-title>
                  <v-card-text style='color:black; margin-top: 2%'>
                    This would be the button you would click to go to the product at Walmart except in order to get approved to use their affiliate program and likewise, their affiliate API to pull URLs and pricing I would need a legit, live, production site.
                  </v-card-text>
                  <v-divider></v-divider>
                  </v-card>
                </v-dialog>
              </div>
          <!--- THIRD BUTTON --->
              <div class='text-right'>
                <v-dialog
                  v-model="dialog"
                  width="500"
                >
                  <template v-slot:activator="{ on }">
                    <v-btn
                      color="grey lighten-5"
                      style='color:black; width:10vw;  text-decoration:none'
                      dark
                      v-on="on"
                    >
                      <v-icon>shopping_cart</v-icon><span style='color:fireBrick; margin-left: 3%'>e</span><span style='color:dodgerblue'>B</span><span style='color: gold'>a</span><span style='color:green; margin-right: 8%'>y</span>  $-
                    </v-btn>
                  </template>
                  <v-card>
                  <v-card-title
                    class="headline grey "
                    primary-title
                    style="text-decoration:none"
                  >
                    <span style='color:fireBrick'>e</span><span style='color:dodgerblue'>B</span><span style='color: gold'>a</span><span style='color:green'>y</span>
                  </v-card-title>
                  <v-card-text style='color:black; margin-top: 2%'>
                    Similar to the others, in order to get approved to use their affiliate program and likewise, their affiliate API to pull URLs and pricing I would need a legit, live, production site.
                  </v-card-text>
                  <v-divider></v-divider>
                </v-card>
              </v-dialog>
            </div>
<!--- END BUTTONS ----> 
          </v-row>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
<!--- END TOP PRODUCT PREVIEW  ---> 
<!--- PRODUCT TABS ---> 
  <v-card>
    <v-tabs
      v-model="tab"
      background-color='blue-grey lighten-3'
      color='black'
      light
    >
      <v-tab id='1' style='color:white'>Description</v-tab>
      <v-tab id='2' style='color:white'>Specs</v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item>
        <v-card flat>
          <v-card-text style='margin-top: 2vh;'>
            <h1 class='description'>{{product.brand.name}} {{product.manufacturerItem}}</h1>
              <ul class='description' style='width:50%;'> 
                <li>Brand: {{product.brand ? product.brand.name : 'Empty'}}</li>
                <li>Manufacturer SKU: {{product.manufacturerItem}}</li>
                <li>Country: {{product.countryOfOrigin}}</li>
                <li>MOQ: {{product.properties.hiscoMinimumOrderQty}}</li>
                <li>{{product.availability.message}}</li>  
             </ul>
          </v-card-text>
        </v-card>
      </v-tab-item>
      <v-tab-item>
        <v-card flat>
          <v-card-title class='text-left'>Specifications</v-card-title>
            <ul class='description'>
              <li v-if='product.attributeTypes[0]'><span style='font-weight:bold'>{{product.attributeTypes[0].label}}:</span> {{product.attributeTypes[0].attributeValues[0].value}} </li>
              <li v-else style='list-style-type: none'></li>

              <li><v-text class='font-weight-bold'> SKU: </v-text> {{product.manufacturerItem}}</li>

              <li v-if='product.attributeTypes[1]'><span style='font-weight:bold'>  {{product.attributeTypes[1].label}}:</span> {{product.attributeTypes[1].attributeValues[0].value}}   </li>
              <li v-else style='list-style-type: none'></li>

              <li v-if='product.attributeTypes[2]'><span style='font-weight:bold'>  {{product.attributeTypes[2].label}}:</span> {{product.attributeTypes[2].attributeValues[0].value}}   </li>
              <li v-else style='list-style-type: none'></li>

              <li v-if='product.attributeTypes[3]'><span style='font-weight:bold'> {{product.attributeTypes[3].label}}:</span> {{product.attributeTypes[3].attributeValues[0].value}}  </li>
              <li v-else style='list-style-type: none'></li>

              <li v-if='product.attributeTypes[4]'><span style='font-weight:bold'>{{product.attributeTypes[4].label}}:</span> {{product.attributeTypes[4].attributeValues[0].value}} </li>
              <li v-else style='list-style-type: none'></li>

              <li v-if='product.attributeTypes[5]'><span style='font-weight:bold'>  {{product.attributeTypes[5].label}}:</span> {{product.attributeTypes[5].attributeValues[0].value}}   </li>
              <li v-else style='list-style-type: none'></li>

              <li v-if='product.attributeTypes[6]'><span style='font-weight:bold'>  {{product.attributeTypes[6].label}}:</span> {{product.attributeTypes[6].attributeValues[0].value}}   </li>
              <li v-else style='list-style-type: none'></li>

              <li v-if='product.attributeTypes[7]'><span style='font-weight:bold'> {{product.attributeTypes[7].label}}:</span> {{product.attributeTypes[7].attributeValues[0].value}}  </li>
              <li v-else style='list-style-type: none'></li>
            </ul>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
  </v-card>
<!---- END PRODUCT TABS ---> 

<!---- Price Chart ---> 


<!--- END ALL ---> 
        </template>
    </div>
</template>

<script>
import API from '@/lib/API'

export default {
    inject: ['theme'],
    name: 'Product',
    data(){
        return{
            product: {},
            value: 0,
            loading: true,
            tab: null,

        }
    },
    created(){
        this.loading = true;
        const { id, collection } = this.$route.params;
        let collectionName = "";
        this.$store.state.categories.forEach((category, idx1) => {
            if (category.id == collection) {
                    collectionName = category.name;
            } else {
                category.children.forEach((subcategory, idx2) => {
                    if (subcategory.id == collection) {
                        collectionName = subcategory.name
                    }
                })
            }
        });
        this.load(id, collectionName)
    },
    methods: {
        total: function(product){
            sum = this.product.properties.mapPrice - this.product.pricing.actualPrice.tofixed(2)
            return value
        },

    load(id, collectionName){     
        API.getProduct(id,collectionName)
            .then((result) => {
              this.loading = false;
              this.product = result
            })
    }
  },
}
</script>

<style scoped>

.card-content {
  padding: 15px 10px 15px 0;
}
.card-content__text {
  padding: 15px 10px 15px 0;
}
.all{
  width: 90%;
}
.description {
  margin-left: 5%;
  padding-bottom: 3vh;
}
.subHead {
  margin-left: 4%;
}
.headline{
  text-decoration: underline;
}
</style>


