<<template>
<div id="app">
  <loading 
    :active.sync="loading"
    background-color="#000000"
    color="#fafafa"
    :opacity="0.8"
    :is-full-page="true"
  />
  <v-app id="inspire">
    <v-navigation-drawer
      v-model="drawer"
      app
      clipped
    >
      <v-list dense>
      <v-list-item>
        <v-list-item-action>
          <v-icon color='black'>home</v-icon>
        </v-list-item-action>
        <v-list-item-content>
          <router-link :to="{path: '/'}">
            <v-list-item-title>Home</v-list-item-title>
          </router-link>
        </v-list-item-content>
      </v-list-item>
      <v-list-item
        v-for="(item, key) in items"
        :key="key"
      >
        <v-list-item-action>
          <v-icon color='black'>{{item.icon}}</v-icon>
        </v-list-item-action>
        <v-list-item-content>
          <router-link :to="{name: 'Category', params: {id: item.id}}"> 
            <v-list-item-title>{{ item.name }} </v-list-item-title>
          </router-link >
        </v-list-item-content>
      </v-list-item>
      
      <v-subheader class="mt-3 grey--text text--darken-1">HELPFUL LINKS</v-subheader>
        <v-list-item
          class="mt-3"
          @click=""
        >
          <v-list-item-action>
            <v-icon color='black'>menu_book</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <router-link :to="{path: '/about'}">
              <v-list-item-title>About</v-list-item-title>
                </router-link>
              </v-list-item-content>
        </v-list-item>
      <v-list-item>
        <v-list-item-action>
          <v-icon color='black'>contacts</v-icon>
        </v-list-item-action>
        <v-list-item-content>
          <router-link :to="{path: '/contact'}">
            <v-list-item-title>Contact</v-list-item-title>
          </router-link>
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>

    <v-app-bar
      app
      clipped-left
      color="blue"
      dense
    >
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-icon class="mx-3">fab fa-youtube</v-icon>
      <v-toolbar-title class="mr-5 align-center">
        <span class="title" ><v-icon large style='margin-right: 1vw;'>mdi-power-socket-de</v-icon>Fynd-It</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
<!------- SIGN UP FORM --------------------> 
      <div >
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        <v-btn
          color="blue darken-2"
          dark
        
          top
          right
          v-on="on"
        >
        <v-icon>mdi-account-plus</v-icon>
         Sign Up
        </v-btn>
      </template>
 <v-card
    class="mx-auto"
    max-width="500"
  >
    <v-card-title class="title font-weight-regular justify-space-between">
      <span>{{ currentTitle }}</span>
      <v-avatar
        color="primary lighten-2"
        class="subheading white--text"
        size="24"
        v-text="step"
      ></v-avatar>
    </v-card-title>

    <v-window v-model="step">
      <v-window-item :value="1">
        <v-card-text>
          <v-text-field
            label="Email"
            value="Example@Example.com"
          ></v-text-field>
          <span class="caption grey--text text--darken-1">
            This is the email you will use to login to your Fynd-It account
          </span>
        </v-card-text>
      </v-window-item>

      <v-window-item :value="2">
        <v-card-text>
          <v-text-field
            label="Password"
            type="password"
          ></v-text-field>
          <v-text-field
            label="Confirm Password"
            type="password"
          ></v-text-field>
          <span class="caption grey--text text--darken-1">
            Please enter a password for your account
          </span>
        </v-card-text>
      </v-window-item>

      <v-window-item :value="3">
        <div class="pa-4 text-center">
          <v-img
            class="mb-4"
            contain
            height="128"
            src="https://cdn.vuetifyjs.com/images/logos/v.svg"
          ></v-img>
          <h3 class="title font-weight-light mb-2">Welcome to Fynd-it</h3>
          <span class="caption grey--text">Obviously this route doesn't work quite yet, but looks pretty cool, right?!</span>
        </div>
      </v-window-item>
    </v-window>

    <v-divider></v-divider>

    <v-card-actions>
      <v-btn
        :disabled="step === 1"
        text
        @click="step--"
      >
        Back
      </v-btn>
      <v-spacer></v-spacer>
      <v-btn
        :disabled="step === 3"
        color="primary"
        depressed
        @click="step++"
      >
        Next
      </v-btn>
    </v-card-actions>
  </v-card>
    </v-dialog>
 </div>

 <!------ SIGN IN -------------------------> 
 <div style='margin-left: 2vw;'>
    <v-dialog
      v-model="signUp"
      width="500"
    >
      <template v-slot:activator="{ on }">
        <v-btn
          color="red darken-3"
          dark
          v-on="on"
        >
        <v-icon>mdi-account-check</v-icon>
          Sign In
        </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="headline grey lighten-2"
          primary-title
        >
         This Doesn't Work Just Yet
        </v-card-title>

        <v-card-text style='color:black; margin-top: 2vh;'>
          Still Working on User Auth with Firebase, but it did give me the opportunity to use this handy dialog box. Thanks Vuetify!         
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="signUp = false"
          >
            That's Fair
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
      </v-window-item>
    </v-window>

      <v-layout
        row
        align-center
        style="max-width: 650px"
      >
      </v-layout>
    </v-app-bar>
    <v-content>
      <router-view  class="container" />
    </v-content>
  </v-app>
</div>

</template>

<script>
import API from '@/lib/API'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';

export default {
  name: 'App',

  props: {
    source: String
  },
  components: {
    Loading
  },
  data: () => ({
    drawer: null,
    items: [],
    step:1,
    dialog:false,
    signUp: false
  }),
  computed: {
    loading() {
      return this.$store.state.loading;
      },
      currentTitle () {
        switch (this.step) {
        case 1: return 'Sign-up'
        case 2: return 'Create a password'
        default: return 'Account created'
      }
    },
  },
  created() {
	this.$vuetify.theme.dark = false
	this.items = this.$store.state.categories;
  }
}
</script>
<style scoped>
.v-list-item__title {
  text-decoration: none;
  color: black;
}
.v-application a {
  text-decoration: none;
}
</style>


