<template>
<div class='all'>
    <h1 style='color:white;'>Contact Us</h1>
    <div class='mainForm'>
    <v-form v-model="valid">
    <v-container>
      <v-row style='justify-content:center;'> 
        <v-col
          cols="12"
          md="7"
        >
          <v-text-field
            v-model="firstname"
            :rules="nameRules"
            :counter="10"
            label="First name"
            required
          ></v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="7"
        >
          <v-text-field
            v-model="lastname"
            :rules="nameRules"
            :counter="10"
            label="Last name"
            required
          ></v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="7"
        >
          <v-text-field
            v-model="email"
            :rules="emailRules"
            label="E-mail"
            required
          ></v-text-field>
        </v-col>

                <v-col
          cols="12"
          md="7"
        >
          <v-text-field

            label="Message"
            required
          ></v-text-field>
        </v-col>

      </v-row>
    </v-container>
    <v-btn color="primary" @click="success = true; error = false;">Send</v-btn>
  </v-form>
</div>
</div>
</template>


<script>
  export default {
    data: () => ({
      valid: false,
      firstname: '',
      lastname: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => v.length <= 10 || 'Name must be less than 10 characters',
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid',
      ],
    }),
  }
</script>


<style scoped>
.all {
background: #4ECDC4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #556270, #4ECDC4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #556270, #4ECDC4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


    height: 100%;
    padding-left: 5%;
}

.mainForm{
    justify-content: center;
    width: 95%;
    background-color: white;
    height: 90%;
}

.container{
    justify-content: center;
    text-align: center;
}

.v-form{
    padding-top: 5%;
}

</style>


