<template>
  <div class="all">
    <v-carousel>
      <v-carousel-item
        v-for="(item,i) in items"
        :key="i"
        :src="item.src"
        reverse-transition="fade-transition"
        transition="fade-transition"
      >
      </v-carousel-item>
    </v-carousel>
    <div style='margin-top: 5vh;'>
    <v-alert
      v-model="alert"
      border="left"
      close-text="Close Alert"
      color="blue darken-1"
      dark
      dismissible
    >
      Welcome to my eCommerce project! This app has been a hobby of mine for the last few months and I am consistently adding to it, trying out new ideas and looking for ways to improve it. This app is made using Vue.js and Firebase, it contains about 12,000 products all of which are templated meaning the core of this app is just 1 category layout page, 1 category product page and 1 product detail page! 

      If you're looking for more about this app and how I've created it see the <a href='/about'><span style=' font-weight:bold;'>About</span></a> page
    </v-alert>
    <div class="text-center">
      <v-btn
        v-if="!alert"
        color="blue darken-4"
        dark
        @click="alert = true"
      >
        View Message Again
      </v-btn>

    </div>
<v-divider style='margin-top 3vh;'></v-divider>
    <h2 class="subHead">Top Categories</h2>
    <v-col cols="12">
      <v-row
        class="grey lighten-5"
        style="height: 300px;"
      >
<!--- CARD 1 --->
      <v-hover v-slot:default="{ hover }" >
        <v-card
          to='/products/solder-tips'
          class="mx-auto"
          max-width="400"
          :elevation="hover ? 16 : 2"
        >
          <v-img
            class="black--text align-end"
            height="200px"
            src="https://images-na.ssl-images-amazon.com/images/I/61HTEfZT0CL._AC_SX522_.jpg"
          >    
          </v-img>
          <v-card-text class="text--primary">
            <h2>Solder Tips</h2>
          </v-card-text>
          <v-card-subtitle class="pb-0">Pace, HAKKO, JBC, Plato and More</v-card-subtitle>
          <v-card-actions>
            <v-btn
              color="blue darken-4"
              text
              to='/products/solder-tips/'
            >
            <v-icon class="mr-1">arrow_right_alt</v-icon>View
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-hover>
<!--- CARD 2 --->
  <v-hover v-slot:default="{ hover }" >
      <v-card
          to='/products/flux-removers'
          class="mx-auto"
          max-width="400"
          :elevation="hover ? 16 : 2"
      >
        <v-img
          class="white--text align-end"
          height="200px"
          src="https://media.digikey.com/Photos/Chemtronics/DEL1692.JPG"
        >
        </v-img>
        <v-card-text class="text--primary">
          <h2>Flux Removers</h2>
        </v-card-text>
      <v-card-subtitle class="pb-0">Chemtronics, Techspray and More</v-card-subtitle>
        <v-card-actions>
          <v-btn
            color="blue darken-4"
            text
            to='/products/solder-tips/'
          >
          <v-icon class="mr-1">arrow_right_alt</v-icon>View
          </v-btn>
        </v-card-actions>
      </v-card>
  </v-hover>
<!--- CARD 3 --->  
      <v-hover v-slot:default="{ hover }" >
      <v-card
          to='/products/wire-cutters'
          class="mx-auto"
          max-width="400"
          :elevation="hover ? 16 : 2"
      >
        <v-img
          class="white--text align-end"
          height="200px"
          src="https://www.tedpella.com/tools_html/oval.jpg"
        >
        </v-img>
        <v-card-text class="text--primary">
          <h2>Wire Cutters</h2>
        </v-card-text>
      <v-card-subtitle class="pb-0">We Offer Over 200 Wire Cutters From Top Brands</v-card-subtitle>
         <v-card-actions>
          <v-btn
            color="blue darken-4"
            text
            to='/products/wire-cutters/'
          >
          <v-icon class="mr-1">arrow_right_alt</v-icon>View
          </v-btn>
        </v-card-actions>
      </v-card>
      </v-hover>
  <!--- CARD 4 --->
       <v-hover v-slot:default="{ hover }" >
      <v-card
          to='/products/thermal-transfer-labels'
          class="mx-auto"
          max-width="400"
          :elevation="hover ? 16 : 2"
      >
        <v-img
          class="white--text align-end"
          height="200px"
          src="data:image/jpeg;base64,"
        >
        </v-img>
        <v-card-text class="text--primary">
          <h2>Thermal Transfer Labels</h2>
        </v-card-text>
      <v-card-subtitle class="pb-0">More Than 300 Choices from Brady, Identco and More </v-card-subtitle>
        <v-card-actions>
          <v-btn
            color="blue darken-4"
            text
            to='/products/thermal-transfer-labels/'
          >
          <v-icon class="mr-1">arrow_right_alt</v-icon>View
          </v-btn>
        </v-card-actions>
      </v-card>
  </v-hover>
  <!--- END CARD 4 --->
      </v-row>
    </v-col>
<!--- SIGN UP FORM ---> 

 <div class="text-center blue darken-4" style='padding-top:10vh;height: 8vh;'>
 </div>

 
    </div>
<!--- END DIV --->
  </div> 
</template>
<script>
export default {
  name: 'Home',
  data() {
    return {
      activeType: 'Flux Removers',
      step:1,
      drawer: '',
      alert: true,
      items: [
          {
            src: 'https://lucidworks.com/wp-content/uploads/2019/05/Digi-Manu-Blog-Header3-1658x468.jpg',
          },
          {
            src: 'https://www.plm.automation.siemens.com/media/global/en/0--NanoBox-Electronics-Digitalization-Image-0-00-00-00-_original_tcm27-52871.jpg',
          },
          {
            src: 'https://upload.wikimedia.org/wikipedia/commons/5/55/Geely_assembly_line_in_Beilun%2C_Ningbo.JPG',
          },
          {
            src: 'https://miro.medium.com/max/3840/1*8DVD3VbhZBD5k6Hw5E5ljQ.jpeg',
          },
        ], 
    }
  },
  created() {
  },
  methods: {
    pushToFirestore(type) {
      this.activeType = type
    }
  },
      computed: {
      currentTitle () {
        switch (this.step) {
          case 1: return 'Sign-up'
          case 2: return 'Create a password'
          default: return 'Account created'
        }
      },
    },
}
</script>

<style scoped>
.subHead{
  margin-top: 4vh;
  margin-bottom: 2vh;
}
.v-card{
  margin-bottom: 10vh;
}

a{
  color:red;
  text-decoration:none;
}
a:hover{
  color:white;
}
</style>


